@import '../../stylesheets/main';
@import '../../stylesheets/theme';

.reactTable{
    border: 1px solid $apl-gray-400;
    @extend .marginMedium;
    border-spacing: 0px;
    th{
        border: 1px solid $apl-gray-400;
        @extend .marginMedium;
        text-transform: $apl-font-table-header-casing;
        font-size: $apl-font-table-header-size;
        padding: $decision-button-padding-x-compact;
        text-align: left;
    }
    td{
        border: 1px solid $apl-gray-400;
        @extend .marginMedium;
        background: $apl-gray-100;
        font-size: $apl-font-paragraph-size;
        line-height: $apl-font-paragraph-line-height;
        color: $apl-gray-800;
        padding: $decision-button-padding-x-compact;
        text-align: left;
    }
}

.searchinput {
    font-size: $apl-font-paragraph-size;
    @extend .marginMedium;
    padding: $decision-button-padding-x-compact;
    width: 25%;
    border: 1px solid $decision-button-secondary-border-default;
}

